<template>
    <a-spin :spinning="loading">
      <a-form-model
        ref="form"
        :model="entity"
        :rules="rules"
        autocomplete="off"
        layout="vertical"
      >
        <div class="card">
          <a-row :gutter="24">
            <a-col :sm="6">
              <a-form-model-item
                label="状态模式名称"
                prop="statusPatternName"
                ref="statusPatternName"
              >
                <a-input
                  v-model="entity.statusPatternName"
                  placeholder="请输入状态模式名称"
                  name="statusPatternName"
                ></a-input>
              </a-form-model-item>
            </a-col>
            <a-col :sm="6">
              <a-form-model-item label="状态" prop="status">
                <a-select
                  v-model="entity.status"
                  placeholder="请选择状态"
                >
                  <a-select-option value="正常">正常</a-select-option>
                  <a-select-option value="警告">警告</a-select-option>
                  <a-select-option value="故障">故障</a-select-option>
                </a-select>
              </a-form-model-item>
            </a-col>
            <a-col :sm="12">
              <a-form-model-item label="描述" prop="description">
                <a-textarea
                  v-model:value="entity.description"
                  placeholder="请输入状态模式描述"
                  :rows="4"
                  :max-length="1000"
                  name="description"
                />
              </a-form-model-item>
            </a-col>
          </a-row>
        </div>
        <g-collapse headerName="转换规则">
          <template #content>
            <a-form-model-item label="转换规则" prop="transitions">
              
              <div v-if="entity.transitions && entity.transitions.length > 0">
                <div v-for="(transition, index) in entity.transitions" :key="index" class="transition-item">
                  <a-select
                    v-model="transition.from"
                    placeholder="起始状态"
                    style="width: 120px"
                    @change="onTransitionChange"
                  >
                    <a-select-option value="正常">正常</a-select-option>
                    <a-select-option value="警告">警告</a-select-option>
                    <a-select-option value="故障">故障</a-select-option>
                  </a-select>
                  <span class="arrow">→</span>
                  <a-select
                    v-model="transition.to"
                    placeholder="目标状态"
                    style="width: 120px"
                    @change="onTransitionChange"
                  >
                    <a-select-option value="正常">正常</a-select-option>
                    <a-select-option value="警告">警告</a-select-option>
                    <a-select-option value="故障">故障</a-select-option>
                  </a-select>
                  <a-icon type="minus-circle-o" class="dynamic-delete-button" @click="removeTransition(index)" />
                </div>
              </div>
              <a-button type="dashed" style="width: 60%" @click="addTransition">
                <a-icon type="plus" /> 添加转换规则
              </a-button>
            </a-form-model-item>
          </template>
        </g-collapse>
        <g-collapse headerName="其他信息">
          <template #content>
            <a-row :gutter="24">
              <a-col :sm="6">
                <!-- 创建人 -->
                <a-form-model-item label="创建人">
                  <span>admin</span >
                </a-form-model-item>
              </a-col>
              <a-col :sm="6">
                <!-- 最近修改时间 -->
                <a-form-model-item label="修改时间">
                  <span> 2025-04-20 15:00</span>
                </a-form-model-item>
              </a-col>
            </a-row>
          </template>
        </g-collapse>
      </a-form-model>
    </a-spin>
  </template>
  
  <script>
  import { GNdoPageEditBase } from "@/utils";
  export default {
    name: "CreateOrEditStatusPattern",
    mixins: [GNdoPageEditBase],
    data() {
      return {
        entity: {
          statusPatternName: "",
          status: "正常",
          description: "",
          transitions: [],
        },
        rules: {
          statusPatternName: [
            { required: true, message: "请输入状态模式名称", trigger: "change" },
          ],
          status: [{ required: true, message: "请选择状态", trigger: "change" }],
        },
      };
    },
  
    mounted() {},
  
    methods: {
      // 初始化
      init() {
        this.loading = true;
        if (this.id) {
          // 模拟请求
          setTimeout(() => {
            this.loading = false;
            this.entity = {
              statusPatternName: "测试状态模式",
              status: "正常",
              description: "这是一个测试状态模式",
              transitions: [
                { from: "正常", to: "警告" },
                { from: "警告", to: "故障" },
              ],
            };
          }, 500);
        } else {
          this.reset();
          this.loading = false;
        }
      },
      resetFormData() {
        // 重置表单绑定的数据源
        this.entity = {
          statusPatternName: "",
          status: "正常",
          description: "",
          transitions: [],
        };
      },
      async save() {
        // 保存 校验
        let res = await this.checkError();
        if (res) {
          this.loading = true;
          setTimeout(() => {
            this.loading = false;
            this.saveSuccess(this.entity.statusPatternName);
            this.handleSaveEndBackView(this.id);
            this.refreshParent({
              reset: null,
            });
          });
        }
      },
      // 添加转换规则
      addTransition() {
        this.entity.transitions.push({
          from: "正常",
          to: "警告",
        });
      },
      // 删除转换规则
      removeTransition(index) {
        this.entity.transitions.splice(index, 1);
      },
      // 转换规则改变时触发，用于确保不会出现循环转换
      onTransitionChange() {
        // 检查是否有循环转换
        for (let i = 0; i < this.entity.transitions.length; i++) {
          for (let j = i + 1; j < this.entity.transitions.length; j++) {
            if (
              this.entity.transitions[i].from === this.entity.transitions[j].to &&
              this.entity.transitions[i].to === this.entity.transitions[j].from
            ) {
              this.$message.warning("不能存在循环转换规则！");
              // 移除其中一个转换规则
              this.entity.transitions.splice(j, 1);
              return;
            }
          }
        }
      }
    },
  };
  </script>
  
  <style lang="scss" scoped>
  .transition-item {
    display: flex;
    align-items: center;
    margin-bottom: 8px;
  }
  
  .arrow {
    margin: 0 8px;
  }
  
  .dynamic-delete-button {
    cursor: pointer;
    position: relative;
    top: 4px;
    font-size: 24px;
    color: #999;
    transition: all 0.3s;
    margin-left: 8px;
  }
  .dynamic-delete-button:hover {
    color: #777;
  }
  </style>