<template>
    <g-body-layout
      :pageIndex="pageNumber"
      :pageSize="pageSize"
      :total="total"
      :filterText.sync="filterText"
      @pageIndexChange="pageIndexChange"
      @pageSizeChange="pageSizeChange"
      @suffixSearchCallback="onFilterReady"
    >
      <!-- 按钮组插槽 -->
      <template #btnGroup>
        <g-btn-group :buttonLists="btnList" @btnCallBack="btnGroupClick">
        </g-btn-group>
      </template>
      <!-- g-ndo-table插槽 -->
      <template #table>
        <g-ndo-table
          :id="id"
          :handName="'设备状态模式'"
          :dataList="dataList"
          :isTableLoading="loading"
          :total="total"
          @rowClick="rowClick"
        >
          <!-- 状态 -->
          <template #status="{ row }">
            <a-tag :color="getStatusColor(row.status)">{{ row.status }}</a-tag>
          </template>
          <!-- 转换规则 -->
          <template #transitions="{ row }">
            <span v-if="row.transitions && row.transitions.length > 0">
              {{ row.transitions.map(t => `${t.from} -> ${t.to}`).join(', ') }}
            </span>
            <span v-else>无</span>
          </template>
        </g-ndo-table>
      </template>
      <!-- 详细内容插槽 -->
      <template #content>
        <create-or-edit-resource-status-model
          :ndoEditParams="ndoEditParams"
          :refreshTag="refreshTag"
          :saveTag="saveTag"
          :resetTag="resetTag"
          @childRefresh="childRefresh($event)"
          @rowClick="rowClick($event)"
          v-if="isShowEdit"
        ></create-or-edit-resource-status-model>
        <g-empty v-else> </g-empty>
      </template>
    </g-body-layout>
  </template>
  
  <script>
  import CreateOrEditResourceStatusModel from "./create-or-edit-resource-status-model/create-or-edit-resource-status-model.vue";
  import GCopyNdo from "@/components/g-copy-ndo/g-copy-ndo.vue";
  import { GNdoPageBase } from "@/utils";
  
  export default {
    name: "DeviceStatusPattern",
    mixins: [GNdoPageBase],
    components: {
      CreateOrEditResourceStatusModel,
    },
  
    data() {
      return {
        isTableLoading: false,
        filterText: "",
      };
    },
  
    mounted() {
      // 添加表格列定义 (在 g-ndo-table 组件中需要支持 slot)
      this.$nextTick(() => {
        if (this.$refs.table && this.$refs.table.setColumns) {
          this.$refs.table.setColumns([
            {
              title: "ID",
              dataIndex: "id",
              key: "id",
              width: 80
            },
            {
              title: "状态模式名称",
              dataIndex: "name",
              key: "name"
            },
            {
              title: "状态",
              dataIndex: "status",
              key: "status",
              scopedSlots: { customRender: "status" } // 使用 slot
            },
            {
              title: "转换规则",
              dataIndex: "transitions",
              key: "transitions",
              scopedSlots: { customRender: "transitions" } // 使用 slot
            },
            {
              title: "描述",
              dataIndex: "description",
              key: "description"
            }
          ]);
        }
      });
    },
    created() {
      this.getBtnList();
    },
    methods: {
      getBtnList() {
        // 获取按钮列表
        this.btnList = [
          {
            text: this.AppConst.button.create,
            icon: this.AppConst.icon.create,
            eventName: this.AppConst.button.create,
            display: true,
          },
          {
            text: this.AppConst.button.copy,
            icon: this.AppConst.icon.copy,
            eventName: this.AppConst.button.copy,
            display: !!this.id,
          },
  
          {
            text: this.AppConst.button.delete,
            icon: this.AppConst.icon.delete,
            eventName: this.AppConst.button.delete,
            display: !!this.id,
          },
          {
            text: this.AppConst.button.refresh,
            icon: this.AppConst.icon.refresh,
            eventName: this.AppConst.button.refresh,
            right: true,
            display: this.isShowEdit,
          },
          {
            text: this.AppConst.button.save,
            icon: this.AppConst.icon.save,
            eventName: this.AppConst.button.save,
            right: true,
            type: "primary",
            display: this.isShowEdit,
          },
        ];
      },
      copy() {
        this.createModal(
          { title: "复制", width: "600px" },
          {
            component: GCopyNdo,
            props: {
              entity: {
                // 这里需要根据实际情况调整
                copyValue: this.dataList[0].name,
                value: this.dataList[0].name,
              },
            },
            on: {
              success: (res) => {
                console.log(res);
                // 手动延时
                this.loading = true;
                setTimeout(() => {
                  this.loading = false;
                  this.copySuccess(res.value, res.copyValue);
                  this.refreshGoFirstPage();
                }, 500);
              },
            },
          }
        );
      },
      delete() {
        // 删除 延时
        this.$confirm({
          title: "确认删除",
          content: "确认删除吗?",
          onOk: () => {
            this.loading = true;
            setTimeout(() => {
              this.loading = false;
              // 消息提示
              this.deleteSuccess();
              // 刷新
              this.refresh();
            }, 500);
          },
        });
      },
      fetchData(request) {
        this.loading = true;
        setTimeout(() => {
          request.finishedCallback();
          // 填充dataList 模拟数据
          let dataList = [];
          for (let i = 0; i < 25; i++) {
            dataList[i] = {
              id: i + 1,
              name: "状态模式" + (i * this.pageNumber + 1),
              status: ["正常", "警告", "故障"][Math.floor(Math.random() * 3)],
              transitions: [
                { from: "正常", to: "警告" },
                { from: "警告", to: "故障" },
              ],
              description: "这是一个状态模式的描述" + i,
            };
          }
  
          // filter 过滤
          if (this.filterText) {
            dataList = dataList.filter((item) => {
              return item.name.indexOf(this.filterText) > -1;
            });
          }
  
          request.successCallback({
            items: dataList,
            totalCount: dataList.length * 2,
          });
        }, 1000);
      },
      // 根据状态返回颜色
      getStatusColor(status) {
        switch (status) {
          case "正常":
            return "green";
          case "警告":
            return "orange";
          case "故障":
            return "red";
          default:
            return "blue";
        }
      },
    },
  };
  </script>
  
  <style lang="scss" scoped></style>